import React from "react"
import { Helmet } from "react-helmet"


 const Meta = (props) => (
    <Helmet>
    <meta charSet="utf-8" />
    <title>Roopam Garg | {props.title}</title>
    <meta name="description" content="Hey, it's Roopam Garg, I'm a full stack developer from India, I have a serious passion for UI
    effects, animation and creating intuitive, dynamic user experiences.
    Let's make something Special."></meta>
    {/* <link rel="canonical" href={`${URL}`} /> */}
  </Helmet>
)
export default Meta